@import '../../assets/css/variable.less';

* {
  box-sizing: border-box;
}

li {
  list-style: none;
}

.bridge-big-box {
  border-top: 1px solid @divider_color;

  .bridge-box {
    width: 760px;
    height: 924px;
    margin: 0 auto;

    .bridge-title {
      display: flex;
      justify-content: space-between;
      height: 96px;
      align-items: center;

      .title {
        font-size: 32px;
        color: #FFFFFF;
      }

      .birdge-des {
        font-size: 14px;
        width: 517px;
      }

    }

    .bridge-info {
      height: 828px;
      border: 1px solid @divider_color;

      .select-network-box {
        height: 220px;
        border-bottom: 1px solid @divider_color;
        padding: 29px 48px 40px 48px;

        .select-network-title {
          color: @whiteFontColor;
          font-size: 18px;
        }

        .select-network-from-to {
          display: flex;
          justify-content: space-between;
          align-items: center;
          margin-top: 29px;

          .to-network-list,
          .from-network-list {
            display: none;
            width: 293px;
            position: absolute;
            background-color: #203B60;
            top: -15px;
            border-radius: 4px;
            padding: 0;
            z-index: 1;

            li {
              width: 293px;
              height: 72px;
              padding-left: 24px;
              display: flex;
              align-items: center;
              justify-content: space-between;
              padding-right: 24px;
              cursor: pointer;

              .li-logo-network {
                display: flex;
                align-items: center;
                color: @whiteFontColor;
                font-size: 18px;

                span {
                  padding-left: 14px;
                }
              }

              .up-icon-btn {
                position: relative;
                z-index: 7;
              }

              .up-icon {
                width: 24px;
                height: 23px;
                transform: rotate(180deg);
                cursor: pointer;
              }

              img {
                width: 22px;
                height: 22px;
              }
            }
          }

          .show {
            display: block;
          }

          .from-network,
          .to-network {

            .from-title,
            .to-title {
              margin-bottom: 8px;
            }

            .from-dropdown-list,
            .to-dropdown-list {
              width: 292px;
              height: 72px;
              position: relative;
              background: rgba(85, 119, 253, 0.1);
              border-radius: 4px;

              .drop {
                width: 292px;
                height: 72px;
                padding-left: 24px;
                padding-right: 24px;
                display: flex;
                align-items: center;
                cursor: pointer;
                justify-content: space-between;

                .down-icon {
                  cursor: pointer;
                }

                .network-logo-connected {
                  display: flex;
                  align-items: center;
                  justify-content: space-between;

                  span {
                    margin-left: 16px;
                    color: @whiteFontColor;
                    font-size: 18px;
                  }

                  .connected,
                  .is-connected {
                    width: 71px;
                    height: 20px;
                    border: 1px solid #59AE99;
                    border-radius: 100px;
                    font-size: 11px;
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    color: #59AE99;
                    margin-left: 8px;
                  }

                  .is-connected {
                    width: 80px;
                    border: 1px solid #CE5E5E;
                    color: #CE5E5E;
                  }

                  .connected-hide {
                    display: none;
                  }
                }
              }

              .drop-disabled {
                .network-logo-connected {
                  span {
                    color: @divider_color;
                  }
                }
              }
            }
          }
        }
      }

      .set-amount {
        height: 212px;
        border-bottom: 1px solid @divider_color;
        padding: 29px 48px 40px 48px;

        .set-amount-title {
          color: @whiteFontColor;
          font-size: 18px;
        }

        .set-amount-box {
          margin-top: 21px;

          .input-deri {
            display: flex;
            justify-content: space-between;
            align-items: center;

            .input-box {
              width: 519px;
              height: 72px;
            }

            span {
              display: flex;
              align-items: center;
              font-size: 20px;
              color: @whiteFontColor;
              letter-spacing: 2px;

              img {
                margin-right: 12px;
              }
            }

            input {
              font-size: 18px;
            }
          }

          .total-deri {
            margin-top: 8px;
          }
        }

      }

      .bridge-operate-hint {
        height: 268px;
        border-bottom: 1px solid @divider_color;
        padding: 29px 48px 40px 48px;

        .operate {
          .from-to-title {
            color: @whiteFontColor;
            text-align: center;
            font-size: 18px;
          }

          .btn {
            display: flex;
            align-items: center;
            justify-content: center;
            margin-top: 25px;

            .button-bridge {
              width: 332px;
              height: 56px;
            }

            .complete {
              height: 56px;
              width: 332px;
              background: rgba(89, 174, 153, 0.1);
              border: 0;
              border-radius: 4px;
              color: #59AE99;
              display: flex;
              cursor: pointer;
              justify-content: center;
              align-items: center;
              img{
                margin-right: 8px;
              }
            }
          }

          .hint {
            margin-top: 27px;

            .sending,
            .claiming {
              width: 332px;
              margin: 0 auto;

              .stage {
                display: flex;
                align-items: center;
                margin-top: 8px;
                justify-content: space-between;

                .stage-img-text {
                  display: flex;
                  justify-content: space-between;
                  align-items: center;

                  .stage-img {
                    width: 10px;
                    height: 10px;
                    border-radius: 50%;
                    border: 1px solid @defaultFontColor;
                    display: flex;
                    align-items: center;
                    justify-content: center;
                  }

                  .stage-text {
                    margin-left: 8px;
                    font-size: 14px;
                  }
                }

                .ok-pending {
                  .pending {
                    width: 14px;
                    height: 14px;
                    background-color: @button_outline_color;
                    border-radius: 50%;
                    display: flex;
                    justify-content: center;
                    align-items: center;

                    .spinner-border {
                      border: 1px solid #000;
                      width: 8px;
                      height: 8px;
                    }
                  }

                  .staic {
                    width: 10px;
                    height: 10px;
                    border-radius: 50%;
                    background: rgba(85, 119, 253, 0.1);
                  }
                }
              }
            }
          }
        }
      }

      .bridge-to-polygon {
        height: 128px;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;

        .birdge-to-polygon-title {
          color: @whiteFontColor;
          font-size: 18px;

          a {
            text-decoration: none;
            color: @button_outline_color;
          }
        }

        .bridge-to-polygon-des {
          font-size: 14px;
          margin-top: 11px;
        }
      }
    }
  }
}

@media only screen and (max-width:375px) {
  .bridge-big-box {
    .bridge-box {
      width: 375px;

      .bridge-title {
        padding-left: 24px;

        .birdge-des {
          display: none;
        }
      }

      .bridge-info {
        border: 0;
        border-top: 1px solid @divider_color;
        height: 976px;

        .select-network-box {
          padding: 37px 24px 40px 24px;
          height: 356px;

          .select-network-from-to {
            display: block;

            .arrow {
              text-align: center;
              margin-top: 20px;
              transform: rotate(90deg);
            }

            .from-network,
            .to-network {

              .from-title,
              .to-title {
                font-size: 14px;
              }

              .from-dropdown-list,
              .to-dropdown-list {
                width: 100%;

                .from-network-list,
                .to-network-list {
                  width: 100%;

                  li {
                    width: 100%;
                  }
                }

                .drop {
                  width: 100%;
                }
              }
            }
          }
        }

        .set-amount {
          padding: 37px 24px 40px 24px;

          .set-amount-box {
            .input-deri {
              .input-box {
                width: 249px;
              }

              span {
                margin-left: 24px;

                .amount-img {
                  display: none;
                }
              }
            }
          }
        }

        .bridge-operate-hint {
          padding: 37px 24px 40px 24px;

          .operate {
            .btn {
              .button-bridge {
                width: 100% !important;
              }
            }
          }
        }

      }
    }
  }
}

