.sleepContentView {
  width: 8px;
  height: 8px;
  background-image: linear-gradient(#000, rgb(134, 84, 84));
  border-radius: 50%;
  text-align: center;
  font-size: 10px;
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: center;
  animation: rotation 1s linear infinite;
}

.sleepContentView .circle {
  width: 6px;
  height: 6px;
  background: #CD7A37;
  border-radius: 50%;

}

@keyframes rotation {
  from {
    transform: rotate(0deg);

  }

  to {
    transform: rotate(360deg);

  }

}